import axios from 'axios'
import { message } from 'antd';
export default class Ajax {
	static ajax = (
		method,
		url,
		headers,
		data,
		baseURL,
		timeout = 5000,
	) => new Promise(
		(resolve, reject) => {
			// Promise:  
			// 
			// resolvereject, JavaScript 
			// resolvepromise
			// rejectpromise
			axios(
				// axiso promise
				method === 'get' ?
					{
						method,
						url,
						headers,
						params: data,
						baseURL,
						timeout,
					} : {
						method,
						headers,
						url,
						data,
						baseURL,
						timeout,
					}
			).then(
				// thenPromise
				//  Promise resolvedthen
				// promiseresolved
				// rejected
				(response) => {
					// response http response.data 
					// http
					if (response.status === 200) {
						let res = response.data
						// 200  
						if (res.status === 200 || res.code === 200 || res.status === 0 || res.code === 0) { //TODO
							// resolvereject Promise 
							// , promise
							// Promise ,
							return resolve(res)
							// console.log('return ')
						} else {
							return message.info(res.message)
						}
					}
					// else {
					// catchPromise 
					// reject catch 
					// return reject(response.data)
					// }
				}
				// Promise catch
				// Promiserejectedcatch
				// thencatch
				// catch Promise then
			).catch(error => {
				let errorInfo = `
				${error.response.data.status} ${error.response.data.error}: ${error.response.data.message} 
				`
				message.error(errorInfo)
			}
				// error.response
			).catch(() => message.error(''))
		}
	)
}
