const FormItem = Form.Item
const Option = Select.Option
const RadioGroup = Radio.Group

// child component FilterFrom
class FilterForm extends React.Component {

  render = () => {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form layout="inline">
        <FormItem label="">
          {
            getFieldDecorator('userType',
              {
                initialValue: ""
              }
            )(
              <Select
                style={{ width: 100 }}
              >
                <Option value=''></Option>
                <Option value="1"></Option>
                <Option value="2"></Option>
                <Option value="3"></Option>
              </Select>
            )
          }
        </FormItem>
        <FormItem label="">
          {
            getFieldDecorator('status',
              {
                initialValue: ""
              }
            )(
              <Select
                style={{ width: 100 }}
              >
                <Option value='' ></Option>
                {/* <Option value={1}></Option> */}
                <Option value="1"></Option>
                <Option value="2"></Option>
                <Option value="3"></Option>
              </Select>
            )
          }
        </FormItem>
        <FormItem>
          <div style={{ display: "inline-block" }}>
            
          <Switch
              checkedChildren=""
              unCheckedChildren=""
              onClick={(checked) => this.props.changeGlobalFilter(checked)}
            />
          </div>
          <Button
            type="primary"
            style={{ margin: '0 20px' }}
            onClick={() => {
              let ruleInfo = this.props.form.getFieldsValue()//object,
              // filterFrom
              let rules = item => (
                ((!ruleInfo.status) || (item.status === ruleInfo.status))
                &&
                ((!ruleInfo.userType) || (item.userType === ruleInfo.userType))
              )
              this.props.changeFilterRules(rules)
            }}
          >
          </Button>
        </FormItem>
      </Form>
    );
  }
}

FilterForm = Form.create({})(FilterForm);

