import React from "react";
import { Table, message, Card, Button, Form, Modal, Input, Select, Tree, Transfer } from "antd";
import Ajax from "../../components/Ajax"
import menuConfig from "./../../config/menuConfig";
import { selectTag, pagination } from "../../utils"
import { getMenus } from '../../components/Api'

const FormItem = Form.Item;
const Option = Select.Option;
const { TreeNode } = Tree;
//  const TreeNode = Tree.TreeNode

export default class PermissionUser extends React.Component {
    state = {
        isRoleVisible: false,
        tableType: "radio",
        selectedItems: [],
    };

    request = () => {
        Ajax.ajax(
            'get',
            '/v1/roles',
            {},
            { page: this.page },
            'https://mook.sunlin.fun/mock/9',
        )
            .then(
                data => {
                    this.setState(
                        () => (
                            {
                                dataSource: data.list,
                                pagination: pagination(data, (current) => {
                                    this.page = current
                                    this.request()
                                }),
                            }
                        )
                    )
                })
            .catch(
                () => message.error("")
            )
    }

    componentWillMount() {
        // 
        this.request()
    }

    // 
    handleRole = () => {
        this.setState({
            isRoleVisible: true
        });
    };

    // 
    handleRoleSubmit = () => {
        const data = this.roleForm.props.form.getFieldsValue();
        this.setState({
            isRoleVisible: false //
        });
        this.roleForm.props.form.resetFields(); // ()
        this.request(); //
    }

    // 
    handlePermission = () => {
        let item = this.state.selectedItems[0]; //
        this.setState({
            isPermVisible: true,
            detailInfo: item,
            // menuInfo: item.menus,
            menuInfo: (getMenus(item.role)).map(item => item.key),
        });
    };

    handlePermEditSubmit = () => {
        //  ,wrappedComponentRef
        let data = this.permForm.props.form.getFieldsValue();
        data.role_key = this.state.selectedItem.key; // key
        data.menus = this.state.menuInfo; // menus

        this.setState({
            isPermVisible: false
        });
        this.request(); //
    }

    // 
    handleUserAuth = () => {
        let item = this.state.selectedItems[0]; //,checkbox 
        this.setState({
            isUserVisible: true,
            detailInfo: item
        });
        // 
        this.getRoleUserList(item.id);
    };

    // 
    getRoleUserList = id => {
        // id: id , id
        Ajax.ajax(
            'get',
            '/v1/role/users',
            {},
            { page: this.page },
            'https://mook.sunlin.fun/mock/9',
        )
            .then(res => {
                if (res) {
                    //,
                    this.getAuthUserList(res.list);
                }
            });
    };

    // 
    getAuthUserList = dataSource => {
        // (,)
        const roleUsers = [];
        const targetKeys = [];
        if (dataSource && dataSource.length > 0) {
            // 
            for (let i = 0; i < dataSource.length; i++) {
                const data = {
                    key: dataSource[i].key,
                    title: dataSource[i].name,
                    role: dataSource[i].role
                };

                console.log(data.role, this.state.selectedItems[0])
                if (data.role === this.state.selectedItems[0].role) {
                    // role,targetKeys
                    targetKeys.push(data.key);
                }
                //
                roleUsers.push(data)
                this.setState(() => ({
                    roleUsers,
                    targetKeys
                }))
            }
        }
    }

    // 
    handleUserSubmit = () => {
        let data = {};
        data.user_ids = this.state.targetKeys;
        data.role = this.state.selectedItems[0].role;
        this.setState({
            isUserVisible: false
        });
        this.request();
    }

    render() {
        const columns = [
            {
                title: "",
                dataIndex: "role_name",
            },
            {
                title: "",
                dataIndex: "status",
                render: (status) => {
                    return status === 0 ? "" : "";
                }
            },
            {
                title: "",
                dataIndex: "role",
                render: (role) =>
                    getMenus(role).map(item => `${item.title}, `)
            },
        ];
        return (
            <div>
                <Card>
                    <Button
                        type="primary"
                        onClick={this.handleRole}>
                        
          </Button>
                    <Button
                        type="primary"
                        style={{ marginLeft: 10, marginRight: 10 }}
                        onClick={this.handlePermission}
                        disabled={this.state.selectedItems.length < 1}
                    >
                        
          </Button>
                    <Button
                        type="primary"
                        onClick={this.handleUserAuth}
                        disabled={!(this.state.selectedItems.length > 0)}
                    >
                        
          </Button>
                </Card>
                <div className="content-wrap">

                    <Table
                        size="small"
                        bordered
                        loading={this.state.loading}
                        columns={columns}
                        //  
                        // scroll={{ 
                        //   x: 1440,
                        //   y: 580 
                        // }}  
                        dataSource={this.state.dataSource}
                        //paginationantdtable10*
                        //   pagination={false} tab le 
                        pagination={this.state.pagination}
                        // pagination={false}
                        // onChange 
                        onChange={(pagination, filters, sorter) => {
                            this.setState(() => ({
                                sortOrder: sorter.order
                            }))
                        }}
                        rowSelection={
                            {
                                type: this.state.tableType,
                                selectedRowKeys: this.state.selectedRowKeys,
                                // onChange
                                onChange: (selectedRowKeys, selectedItems) => {
                                    this.setState(() => ({
                                        selectedRowKeys,
                                        selectedItems,
                                    }),
                                    )
                                }
                            }
                        }
                        onRow={(selectedItem) => ({
                            onClick: () => {
                                if (this.state.tableType === 'checkbox') {
                                    let selectedItems = selectTag([...this.state.selectedItems], [selectedItem])
                                    let selectedRowKeys = selectTag([...this.state.selectedRowKeys], [selectedItem.key])
                                    this.setState(() => ({
                                        selectedRowKeys,
                                        selectedItems,
                                    }))
                                }
                                if (this.state.tableType === 'radio') {
                                    let selectedItems = [selectedItem]
                                    let selectedRowKeys = [selectedItem.key]
                                    this.setState(() => ({
                                        selectedRowKeys,
                                        selectedItems,
                                    }))
                                }
                            },
                            onMouseEnter: () => { },
                        })
                        }
                    />
                </div>
                <Modal
                    title=""
                    visible={this.state.isRoleVisible}
                    onOk={this.handleRoleSubmit}
                    onCancel={() => {
                        this.roleForm.props.form.resetFields(); // 
                        this.setState({
                            isRoleVisible: false
                        });
                    }}
                >
                    <RoleForm
                        wrappedComponentRef={inst => {
                            this.roleForm = inst;
                        }}
                    />
                </Modal>
                <Modal
                    title=""
                    visible={this.state.isPermVisible}
                    width={600}
                    onOk={this.handlePermEditSubmit}
                    onCancel={() => {
                        this.setState(() => ({
                            isPermVisible: false
                        }));
                    }}
                >
                    <PermEditForm
                        wrappedComponentRef={inst => {
                            this.permForm = inst;
                        }}
                        detailInfo={this.state.detailInfo}
                        menuInfo={this.state.menuInfo}
                        patchMenuInfo={checkedKeys => {
                            this.setState({
                                menuInfo: checkedKeys
                            });
                        }}
                    />
                </Modal>
                <Modal
                    title=""
                    visible={this.state.isUserVisible}
                    width={800}
                    onOk={this.handleUserSubmit}
                    onCancel={() => {
                        this.setState({
                            isUserVisible: false
                        });
                    }}
                >
                    <RoleAuthForm
                        wrappedComponentRef={inst => {
                            this.userAuthForm = inst;
                        }}
                        detailInfo={this.state.detailInfo}
                        targetKeys={this.state.targetKeys}
                        roleUsers={this.state.roleUsers}
                        patchUserInfo={(targetKeys) => {
                            this.setState({ targetKeys });
                        }}
                    />
                </Modal>
            </div>
        );
    }
}

// -------
class RoleForm extends React.Component {
    render() {
        const formItemLayout = {
            labelCol: {
                span: 5
            },
            wrapperCol: {
                span: 19
            }
        };
        const { getFieldDecorator } = this.props.form;
        return (
            <Form layout="horizontal">
                <FormItem label="" {...formItemLayout}>
                    {getFieldDecorator("role")(
                        <Input type="text" placeholder="" />
                    )}
                </FormItem>
                <FormItem label="" {...formItemLayout}>
                    {getFieldDecorator("state")(
                        <Select>
                            <Option value={1}></Option>
                            <Option value={2}></Option>
                        </Select>
                    )}
                </FormItem>
            </Form>
        );
    }
}

RoleForm = Form.create({})(RoleForm);

// ---------
class PermEditForm extends React.Component {
    onCheck = checkedKeys => {
        //   PermEditForm
        this.props.patchMenuInfo(checkedKeys);
    };

    // 
    /**
     *
     * @param data:menuConfig.js 
     */
    renderTreeNodes = data => {
        // ,children,
        return data.map(item => {
            if (item.children) {
                // 
                return (
                    <TreeNode title={item.title} key={item.key}>
                        {this.renderTreeNodes(item.children)}
                    </TreeNode>
                );
            } else {
                return <TreeNode title={item.title} key={item.key} />;
                // 
                // return <TreeNode {...item}/>
            }
        });
    };

    render() {
        const formItemLayout = {
            labelCol: {
                span: 5
            },
            wrapperCol: {
                span: 19
            }
        };
        const { getFieldDecorator } = this.props.form;
        const detail_info = this.props.detailInfo;
        const menu_info = this.props.menuInfo;
        return (
            <Form layout="horizontal">
                <FormItem label="" {...formItemLayout}>
                    <Input disabled placeholder={detail_info.role_name} />
                </FormItem>
                <FormItem label="" {...formItemLayout}>
                    {getFieldDecorator("status", {
                        initialValue: detail_info.status + ""
                    })(
                        <Select>
                            <Option value="1"></Option>
                            <Option value="0"></Option>
                        </Select>
                    )}
                </FormItem>
                <Tree
                    checkable
                    defaultExpandAll
                    onCheck={checkedKeys => {
                        // checkedKeys: 
                        this.onCheck(checkedKeys);
                    }}
                    checkedKeys={menu_info}
                >
                    <TreeNode title="" key="platform_all">
                        {this.renderTreeNodes(menuConfig)}
                    </TreeNode>
                </Tree>
            </Form>
        );
    }
}

PermEditForm = Form.create({})(PermEditForm);

// ---------
class RoleAuthForm extends React.Component {
    filterOption = (inputValue, option) => {
        return option.title.indexOf(inputValue) > -1;
    };

    handleChange = (targetKeys) => {
        this.props.patchUserInfo(targetKeys);
    };
    render() {
        const formItemLayout = {
            labelCol: {
                span: 5
            },
            wrapperCol: {
                span: 19
            }
        };
        const detail_info = this.props.detailInfo;
        return (
            <Form layout="horizontal">
                <FormItem label="" {...formItemLayout}>
                    <Input disabled placeholder={detail_info.role_name} />
                </FormItem>

                <FormItem label="" {...formItemLayout}>
                    <Transfer
                        listStyle={{ width: 250, height: 400 }}
                        dataSource={this.props.roleUsers}
                        titles={["", ""]}
                        showSearch
                        filterOption={this.filterOption} //
                        targetKeys={this.props.targetKeys} //
                        onChange={this.handleChange} //
                        render={item => item.title} //
                    />
                </FormItem>
            </Form>
        );
    }
}
RoleAuthForm = Form.create({})(RoleAuthForm);