import React from 'react';
import {
  Card,
  Form,
  Button,
  Input,
  Radio,
  Checkbox,
  message,
} from "antd";
import Ajax from '../../components/Ajax'
import '../../style/common.scss'
import {goToUrl} from '../../utils'

const FormItem = Form.Item;
const RadioGroup = Radio.Group;

class FormRegister extends React.Component {

  post = (formData) => {
    Ajax.ajax(
      'post',
      '/user-register',
      {},
      formData,
      "http://207.148.65.10:8080",
    )
      .then(() => {message.info("")})
      // .catch(() => { })
  }

  passwordValidator = (rule, value, callback) => {
    let password = this.props.form.getFieldsValue().password
    console.log(rule, value, password)
    if (value && value !== password) {
      callback('')
    }
    callback()
  }

  handleSubmit = () => {
    let formData = this.props.form.getFieldsValue();// ()object
    this.post(formData)


  };
  getBase64 = (img, callback) => {
    const reader = new FileReader();
    reader.addEventListener('load', () => callback(reader.result));
    reader.readAsDataURL(img);
  };

  handleChange = (info) => {
    if (info.file.status === 'uploading') {
      this.setState({ loading: true });
      return;
    }
    if (info.file.status === 'done') {
      // Get this url from response in real world.
      this.getBase64(info.file.originFileObj, imageUrl => this.setState({
        userImg: imageUrl,
        loading: false,
      }));
    }
  };

  componentDidMount = () => {

  }

  render() {
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: 24,
        sm: 4
      },
      wrapperCol: {
        xs: 24,
        sm: 12
      }
    };
    const offsetLayout = {
      wrapperCol: {
        xs: 24,
        sm: {
          span: 12,
          offset: 4
        }
      }
    };


    return (
      <div>
        <Card title="">
          <Form layout="horizontal">
            <FormItem label="" {...formItemLayout}>
              {
                getFieldDecorator('userName', {
                  initialValue: '',
                  rules: [
                    {
                      required: true,
                      message: ''
                    },
                    {
                      min: 1, max: 16,
                      message: ''
                    },
                    {
                      pattern: new RegExp('^\\w+$', 'g'),
                      message: ''
                    }
                  ]
                })(
                  <Input placeholder="" />
                )
              }
            </FormItem>
            <FormItem label="" {...formItemLayout}>
              {
                getFieldDecorator('password', {
                  initialValue: '',
                  rules: [
                    {
                      required: true,
                      message: ''
                    },
                  ]

                })(
                  <Input placeholder="" />
                )
              }
            </FormItem>
            <FormItem label="" {...formItemLayout}>
              {
                getFieldDecorator('repeat', {
                  rules: [
                    {
                      required: true,
                      message: '',
                    },
                    {
		      validator: this.passwordValidator,
                    }
                  ],
                },
                )(
                  <Input placeholder="" />
                )
              }
            </FormItem>
            <FormItem label="" {...formItemLayout}>
              {
                getFieldDecorator('userType', {
                  initialValue: "0",
                  rules: [{
                    required: true,
                    message: ''
                  },
                  ]
                }
                )(
                  <RadioGroup>
                    <Radio value={0}></Radio>
                    <Radio value={1}></Radio>
                    <Radio value={2}></Radio>
                  </RadioGroup>
                )
              }
            </FormItem>
            <FormItem label="" {...formItemLayout}>
              {
                getFieldDecorator('phone', {
                  initialValue: '',
                  rules: [
                    {
                      required: true,
                      message: ''
                    },
                    {
                      pattern: new RegExp('^\\d+$', 'g'),
                      message: ''
                    }
                  ]
                })(
                  <Input placeholder="" />
                )
              }
            </FormItem>
            <FormItem label="" {...formItemLayout}>
              {
                getFieldDecorator('email', {
                  initialValue: '',
                  rules: [
                    {
                      required: true,
                      message: ''
                    },
                    {
                      pattern: new RegExp(/^[\w-]+(\.[\w-]+)*@[\w-]+(\.[\w-]+)+$/),
                      message: ''
                    }
                  ]
                })(
                  <Input placeholder="" />
                )
              }
            </FormItem>
            <FormItem label="" {...formItemLayout}>
              {
                getFieldDecorator('idcard', {
                  initialValue: '',
                  rules: [
                    {
                      required: true,
                      message: ''
                    },
                    {
                      pattern: new RegExp(/^(\d{6})(\d{4})(\d{2})(\d{2})(\d{3})([0-9]|X)$/),
                      message: ''
                    }
                  ]
                })(
                  <Input placeholder="" />
                )
              }
            </FormItem>
            <FormItem {...offsetLayout}>
              {
                getFieldDecorator('register', {
                  valuePropName: 'checked',
                  initialValue: true,
                })(
                  <Checkbox><button className="link-button" href="#" ></button></Checkbox>
                )
              }
            </FormItem>
            <FormItem {...offsetLayout}>
              <Button type="primary" onClick={this.handleSubmit}></Button>
              <Button type="primary" onClick={()=>goToUrl('/login')}></Button>
            </FormItem>
          </Form>
        </Card>
      </div>
    )
  }
}

export default Form.create()(FormRegister);
