// 
export const actionTypes = {
    CLEAR_LOGIN_DATA: 'clear_login_data',
    SAVE_LOGIN_DATA: 'save_login_data',
}

export const actionCreator = {
    clearLoginData: () => ({
        type: actionTypes.CLEAR_LOGIN_DATA,
    }),

    saveLoginData: (data) => ({
        type: actionTypes.SAVE_LOGIN_DATA,
        data,
    }),
}