import React from "react";
import { connect } from "react-redux"
import { actionCreator } from "../../redux/action"
import { Button, Card, Form, Icon, Input, Checkbox, message } from "antd";
import { goToUrl } from "../../utils"; //
import Ajax from '../../components/Ajax'

const FormItem = Form.Item;
class FormLogin extends React.Component {
  post = (formData) => {
    Ajax.ajax(
      'post',
      '/user-login',
      {},
      formData,
      "http://207.148.65.10:8080",
    )
      .then(
        (res) => {
          this.props.saveLoginData(res.data)
          sessionStorage.setItem("token", res.data.token);
          if (this.autoLogin) {
            //tokenlocalStorage
            localStorage.setItem("token", res.data.token);
          } else {
            localStorage.removeItem("token");
          }
          goToUrl('/home')
        }
      ).catch(() => { })
  }

  handleSubmit = () => {//
    let formData = this.props.form.getFieldsValue()// ()object
    this.props.form.validateFields((err, values) => {
      if (!err) {// ${}  
        this.autoLogin = formData.autoLogin
        this.post(formData)
      }
    });
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    const offsetLayout = {
      wrapperCol: {
        xs: 24,
        sm: {
          span: 12,
          offset: 4
        }
      }
    };
    const formItemLayout = {
      labelCol: {
        xs: 24,
        sm: 4
      },
      wrapperCol: {
        xs: 24,
        sm: 12
      }
    };
    return (
      <div>
        <Card title=""
        >
          <Form
            layout="horizontal"
          >
            <FormItem label="" {...formItemLayout}>
              {
                getFieldDecorator('phone', {
                  initialValue: '',
                  rules: [
                    {
                      pattern: new RegExp('^\\d+$', 'g'),
                      message: ''
                    },
                    {
                      required: true,
                      message: ''
                    },
                  ]
                })(
                  <Input placeholder="" />
                )
              }
            </FormItem>
            <FormItem
              label=""
              {...formItemLayout}
            >
              {
                getFieldDecorator('password', {
                  initialValue: '',
                  rules: [{
                    required: true,
                    message: ''
                  }]
                })(
                  <Input prefix={<Icon type="lock" />} type="password" placeholder="" />
                )
              }

            </FormItem>
            <FormItem  {...offsetLayout}>
              {
                getFieldDecorator('autoLogin', {
                  valuePropName: 'checked',
                  initialValue: true,
                })(
                  <Checkbox></Checkbox>
                )
              }
            </FormItem>
            <FormItem
              style={{
                marginLeft: 'auto',
                marginRight: 'auto',
                width: 200,
              }}
            >
              <Button type="primary" onClick={this.handleSubmit}></Button>
              <Button type="primary" onClick={()=>goToUrl('/register')}></Button>
            </FormItem>
          </Form>
        </Card>
      </div >
    )
  }
}

// props 
const mapStateToProps = (state) => ({
  isLogin: state.isLogin,
  user: state.user
})

// props 
const mapDispatchToProps = (dispatch) => {
  return {
    saveLoginData(data) {
      dispatch(actionCreator.saveLoginData(data))
    },
  }
}

// UI
export default connect(mapStateToProps, mapDispatchToProps)(Form.create()(FormLogin))