import React from "react";
import { connect } from "react-redux"
import { actionCreator } from "../../redux/action"
import { Button, Card, Form, Icon, Input, Checkbox, message, Radio } from "antd";
import { goToUrl } from "../../utils"; //
import Ajax from '../../components/Ajax'

const RadioGroup = Radio.Group;
const FormItem = Form.Item;
class FormPublish extends React.Component {
  post = (formData) => {
    Ajax.ajax(
      'post',
      '/user-login',
      {},
      formData,
      "http://207.148.65.10:8080",
    )
      .then(
        (res) => {
        }
      ).catch(() => { })
  }

  handleSubmit = () => {//
    let formData = this.props.form.getFieldsValue()// ()object
    this.props.form.validateFields((err, values) => {
      if (!err) {// ${}  
        this.autoLogin = formData.autoLogin
        this.post(formData)
      }
    });
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    const offsetLayout = {
      wrapperCol: {
        xs: 24,
        sm: {
          span: 12,
          offset: 4
        }
      }
    };
    const formItemLayout = {
      labelCol: {
        xs: 24,
        sm: 4
      },
      wrapperCol: {
        xs: 24,
        sm: 12
      }
    };
    return (
      <div>
        <Card title=""
        >
          <Form
            layout="horizontal"
          >
            <FormItem label="" {...formItemLayout}>
              {
                getFieldDecorator('phone', {
                  initialValue: '',
                  rules: [
                    {
                      pattern: new RegExp('^\\d+$', 'g'),
                      message: ''
                    },
                    {
                      required: true,
                      message: ''
                    },
                  ]
                })(
                  <Input placeholder="" />
                )
              }
            </FormItem>
            <FormItem label="" {...formItemLayout}>
              {
                getFieldDecorator('adType', {
                  initialValue: "0",
                  rules: [{
                    required: true,
                    message: ''
                  },
                  ]
                }
                )(
                  <RadioGroup>
                    <Radio value={0}></Radio>
                    <Radio value={1}></Radio>
                  </RadioGroup>
                )
              }
            </FormItem>
            <FormItem
              style={{
                marginLeft: 'auto',
                marginRight: 'auto',
                width: 200,
              }}
            >
              <Button type="primary" onClick={this.handleSubmit}></Button>
            </FormItem>
          </Form>
        </Card>
      </div >
    )
  }
}

// props 
const mapStateToProps = (state) => ({
  isLogin: state.isLogin,
  user: state.user
})

// props 
const mapDispatchToProps = (dispatch) => {
  return {
    saveLoginData(data) {
      dispatch(actionCreator.saveLoginData(data))
    },
  }
}

// UI
export default connect(mapStateToProps, mapDispatchToProps)(Form.create()(FormPublish))