import React from "react";
import { connect } from "react-redux"
import { actionCreator } from "../../redux/action"
import Logo from "./logo.svg"
import { Row, Col, Button } from "antd";
import "./index.scss";
import { goToUrl } from "../../utils"; //


class Header extends React.Component {
    // state setState
    render() {
        return (
            <div className="header">
                <Row className="header-top">
                    <Col span={6} className="logo">
                        <img src={Logo} alt="" />
                        <span>Logo</span>
                    </Col>
                    <Col span={18} style={{ float: "right" }}>
                        {
                            this.props.isLogin ?
                                <span>
                                    <Button>{this.props.user.userName}</Button>
                                    <Button onClick={() => {
                                        goToUrl('/login')
                                        //  token
                                        sessionStorage.removeItem("token")
                                        localStorage.removeItem("token")
                                        this.props.clearLoginData()
                                    }}
                                    ></Button>
                                </span>
                                :
                                <span>
                                    <Button onClick={() => goToUrl('/login')}></Button>
                                    <Button onClick={() => goToUrl('/register')}></Button>
                                </span>
                        }
                    </Col>
                </Row>
            </div>
        );
    }
}

// props 
const mapStateToProps = (state) => ({
    isLogin: state.isLogin,
    user: state.user
})

// props 
const mapDispatchToProps = (dispatch) => {
    return {
        clearLoginData() {
            dispatch(actionCreator.clearLoginData())
        },
    }
}

// UI
export default connect(mapStateToProps, mapDispatchToProps)(Header)