class userTable extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
        }
        this.formList = [
            {
                type: 'SELECT',
                label: '',
                field: 'type',
                placeholder: '',
                initialValue: '',
                width: 100,
                list: [{ id: '', name: '' }, { id: '1', name: '' }, { id: '2', name: '' }]
            },
            {
                type: 'SELECT',
                label: '',
                field: 'status',
                placeholder: '',
                initialValue: '1',
                width: 100,
                list: [{ id: '0', name: '' }, { id: '1', name: '' }, { id: '2', name: '' }, { id: '3', name: '' }, { id: '4', name: '' }]
            },
        ];
    }


    render = () => {
        const columns = [
            {
                title: 'id',
                dataIndex: 'id'
            }, {
                title: '',
                dataIndex: 'username'
            }, {
                title: '',
                dataIndex: 'sex',
                render(sex) {
                    return sex === 1 ? '' : '';
                }
            }, {
                title: '',
                dataIndex: 'state',
                render(state) {
                    let config = {
                        '1': "",
                    };
                    return config[state];
                }
            },
            {
                title: '',
                dataIndex: 'birthday'
            }, {
                title: '',
                dataIndex: 'address'
            },
        ];

    }
}